.root {
}

.header {
    text-align: center;

    > h1 {
        font-weight: 600;
        font-size: 24px;
        line-height: 1.25;
        margin: 0 0 30px 0;
    }
}

.footer {
    font-size: 15px;
    line-height: 1.4;
    color: #8e9196;
    text-align: center;
    margin: 30px 0 0 0;

    > span {
        display: block;
        width: 100%;
        margin: 0 0 5px 0;

        > a {
            font-weight: 500;
        }
    }
}
