.root {
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    background-color: #f7f7fa;
    padding: 16px;
}

.contentContainer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    width: 350px;
    max-width: 100%;
    margin: auto;
}

.header {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 30px 0;
}

.logo {
    display: block;
    width: 140px;
    color: #000;

    > svg {
        display: block;
        width: 100%;
        height: auto;
    }
}

.pane {
    width: 100%;
    background-color: #fff;
    padding: 30px;
    border-radius: 16px;
    box-shadow: 0 0 60px rgba(0, 0, 0, 0.1);
    margin: 0 0 70px 0;
    position: relative;
}
