.root {
}

.tip {
    font-weight: 600;
    font-size: 15px;
    line-height: 1.4;
    color: #8e9196;
    margin: 40px 0 0 0;
    white-space: pre-wrap;

    a {
        font-weight: 600;
    }
}

.recaptcha {
    display: block;
    width: 100%;
    padding: 0 16px;
    font-size: 12px;
    line-height: 1.3;
    text-align: center;
    color: #8e9196;
    opacity: 0.7;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(calc(100% + 16px));

    a {
        font-weight: normal;
    }
}
