.root {
    display: flex;
    align-items: center;
    padding: 5px 24px;
    min-height: 45px;
    background-color: #fff;
    user-select: none;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.02px;
    position: relative;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.08);
    cursor: pointer;

    &.hasIcon {
        padding: 5px 16px 5px 66px;
    }

    .icon {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 50px;
        height: 100%;
        border-right: 1px solid darken(#fff, 4);

        > svg {
            display: block;
            flex-shrink: 0;
        }
    }

    .content {
        flex: 1;
        max-width: 100%;
        text-align: left;

        .subTitle {
            font-weight: normal;
            font-size: 11px;
            line-height: 1;
            opacity: 0.5;
            white-space: nowrap;
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

.root + .root {
    margin-top: 16px;
}
